import React, { Component } from 'react';
import { SlideShowButton } from './SlideShowButton';
import { LivePreview } from './LivePreview';


export class SlideShowApp extends Component{
    constructor(props, context) {
        super(props, context);

        this.state = {
            text: ''
        };
        
        this._handleChange = this._handleChange.bind(this);
    }

    componentWillUnmount(){
    }

    componentDidMount(){
    }

    _handleChange(e) {
        this.setState({
            text: e.target.value 
        });
    }

    render() {
        return(
            React.createElement("div", {className: "row"}, 
                React.createElement("div", {className: "col-md-6"}, 
                    React.createElement(SlideShowButton, {text: this.state.text, className: "btn btn-primary"}), 
                    React.createElement("textarea", {className: "form-control", rows: "25", onChange:  this._handleChange})
                ), 
                React.createElement("div", {className: "col-md-6"}, 
                    React.createElement(LivePreview, {text: this.state.text})
                )
            )
        );
    }
}

SlideShowApp.defaultProps = {
    
}